.app-component {
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    a {
        text-decoration: none;
    }
    a, a:visited, a:hover, a:active {
        color: inherit;
    }
}

@media screen and (min-width: 600px) {
    .app-component {
        width: 70%;
        margin-bottom: 150px;
    }
}


